<template>
	<view class="signature">
		<view class="inputbox">
			<textarea value="" v-model="user.signature" :placeholder="details.placeholder" />
		</view>
		<button type="warn" class="mini-btn" @click="btn">{{details.btn}}</button>
	</view>
</template>

<script>
	import json from "../../../static/json/signature.json"
	export default{
		data(){
			return{
				user:'',
				details:''
			}
		},
		onLoad() {
			uni.getStorage({
				key:'lang',
				success:res=>{
					this.details=json[res.data]
					uni.setNavigationBarTitle({
						title:json[res.data].title
					})
				}
			})
			uni.getStorage({
				key:'userInfo',
				success:res=>{
					this.user=res.data
				}
			})
		},
		methods:{
			btn(){
				console.log(this.user)
				uni.setStorage({
					key:"userInfo",
					data:this.user,
					success:res=>{
						if(res.errMsg="setStorage:ok"){
							uni.showToast({
								title: '保存',
								duration: 2000,
								icon:"success"
							});
						}
					}
				})
			}
		}
	}
</script>

<style>
	.signature{
		width: 100%;
		height: 100vh;
		background: #f4f4f4;
	}
	.inputbox{
		width: 90%;
		margin: auto;
		padding: 5% 0;
	}
	.inputbox textarea{
		width: 95%;
		background: #FFFFFF;
		border-radius: 5px;
		padding: 10px;
	}
	.mini-btn{
		margin: auto;
		width: 90%;
	}
</style>
